@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
    src        : url('../fonts/#{$url}..eot') format("eot");
    src        : url('../fonts/#{$url}.woff2') format("woff2"),
    url("../fonts/#{$url}.ttf") format("truetype");
    font-weight : #{$weight};
    font-display: swap;
    font-style  : $style;
  }
}
@include font-face("Antenna-Medium", "../fonts/FordAntenna-Regular", 400, normal);
@include font-face("Muller", "../fonts/MullerRegular", 400, normal);
@include font-face("Antenna-Bold", "../fonts/FordAntenna-Bold", 700, normal);


@include font-face("Gilroy-Regular", "../fonts/Gilroy-Regular", 400, normal);
@include font-face("Gilroy-Bold", "../fonts/Gilroy-Bold", 700, normal);
@include font-face("Gilroy-SemiBold", "../fonts/Gilroy-SemiBold", 600, normal);

